// 获取应用实例
var app = getApp().globalData;

export default {
  data() {
    return {
      motto: "环信即时通讯云",
      userInfo: {},
      login: false
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    var me = this;
    this.timeOut = setTimeout(function () {
      wx.redirectTo({
        url: "../login/login"
      });
    }, 3000); // 调用应用实例的方法获取全局数据

    getApp().globalData.getUserInfo(function (userInfo) {
      // 更新数据
      me.setData({
        userInfo: userInfo
      });
    });
  },
  methods: {
    // 事件处理函数
    bindViewTap: function () {
      clearTimeout(this.timeOut);
      wx.redirectTo({
        url: "../login/login"
      });
    }
  }
};
